import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import SingleProperty from "./components/SingleProperty"

const FeaturedProperties = () => {
  const data = useStaticQuery(graphql`
    query MyQuery {
      wordpress {
        properties {
          nodes {
            PropertyOptions {
              homefeatured
            }
            title
            slug
            PropertyBasics {
              status
              price
              status
              bedrooms
              bathrooms
            }
            PropertyImages {
              mainimage {
                sourceUrl
                altText
              }
              gallery {
                sourceUrl
                altText
              }
            }
          }
        }
      }
    }
  `)

  return (
    <section className="gray">
      <div className="container">
        <div className="row">
          <div className="col-lg-12 col-md-12">
            <div className="sec-heading center">
              <h2>Browse All Property</h2>
              <p>Popular properties by our expert agents</p>
            </div>
          </div>
        </div>

        <div className="row">
          {data.wordpress.properties.nodes
            .filter(node => {
              if (node.PropertyOptions.homefeatured === true) {
                return true
              } else {
                return false
              }
            })
            .map((featured, index) => {
              return <SingleProperty key={index} {...featured} />
            })}
        </div>
      </div>
    </section>
  )
}

export default FeaturedProperties
