import React from "react"
import { Link } from "gatsby"
import Slider from "react-slick"
import { FormattedNumber } from "react-intl"
import { link } from "../../../../App"

const SingleProperty = ({ title, slug, PropertyBasics, PropertyImages }) => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  }
  return (
    <div className="col-lg-4 col-md-6 col-sm-12">
      <div className="property-listing property-2">
        <div className="listing-img-wrapper">
          <div className="list-img-slide">
            <div className="click">
              <Slider {...settings}>
                <div>
                  <Link to={`/property/${slug}`}>
                    <img
                      src={`${PropertyImages.mainimage.sourceUrl}`}
                      className="img-fluid mx-auto"
                      alt=""
                    />
                  </Link>
                </div>
                {PropertyImages.gallery.map((image, index) => {
                  return (
                    <div key={index}>
                      <Link to={`/property/${slug}`}>
                        <img
                          src={`${image.sourceUrl}`}
                          className="img-fluid mx-auto"
                          alt=""
                        />
                      </Link>
                    </div>
                  )
                })}
              </Slider>
            </div>
          </div>
          <span className="property-type">For {PropertyBasics.status}</span>
        </div>

        <div className="listing-detail-wrapper pb-0">
          <div className="listing-short-detail">
            <h4 className="listing-name">
              <Link to={`/property/${slug}`}>{title}</Link>
              <i className="list-status ti-check"></i>
            </h4>
          </div>
        </div>

        <div className="price-features-wrapper">
          <div className="listing-price-fx">
            <h6 className="listing-card-info-price price-prefix">
              <FormattedNumber
                value={
                  Object.keys(PropertyBasics).includes("price")
                    ? PropertyBasics.price
                    : 0
                }
                style="currency"
                currency="CAD"
              />
              {PropertyBasics.status === "rent" ? (
                <span className="price-suffix">/mo</span>
              ) : null}
            </h6>
          </div>
          <div className="list-fx-features">
            <div className="listing-card-info-icon">
              <span className="inc-fleat inc-bed">
                {Object.keys(PropertyBasics).includes("bedrooms")
                  ? PropertyBasics.bedrooms
                  : 0}{" "}
                Beds
              </span>
            </div>
            <div className="listing-card-info-icon">
              <span className="inc-fleat inc-bath">
                {Object.keys(PropertyBasics).includes("bathrooms")
                  ? PropertyBasics.bathrooms
                  : 0}{" "}
                Bath
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SingleProperty
